import React, { Component } from "react";
import { Route, Redirect } from "react-router-dom";
import { connect } from "react-redux";

import CheckoutSummary from "../../components/Order/ChekoutSummary/CheckoutSummary";
import ContactData from "./ContactData/ContactData";

class Checkout extends Component {
  // When we get data through query params
  // componentDidMount() {
  //   const query = new URLSearchParams(this.props.location.search);
  //   const burgerIngredients = {};
  //   let burgerPrice;

  //   query.forEach((quantity, param) => {
  //     if (param !== "price") {
  //       burgerIngredients[param] = Number(quantity);
  //     }

  //     if (param === "price") {
  //       burgerPrice = +quantity;
  //     }
  //   });

  //   this.setState({ ingredients: burgerIngredients, totalPrice: burgerPrice });
  // }

  goBack = () => {
    this.props.history.goBack();
  };

  redirectContactHandler = () => {
    this.props.history.replace(`${this.props.match.path}/contact-data`);
  };

  render() {
    let summary = <Redirect to="/" />;
    if (this.props.ingredients) {
      summary = (
        <div>
          <CheckoutSummary
            ingredients={this.props.ingredients}
            cancelCheckoutHandler={this.goBack}
            continueCheckoutHandler={this.redirectContactHandler}
          />
          <Route
            path={`${this.props.match.path}/contact-data`}
            component={ContactData}
          />
        </div>
      );
    }
    return summary;
  }
}

const mapStateToProps = (state) => {
  return {
    ingredients: state.burgerBuilder.ingredients,
  };
};

export default connect(mapStateToProps)(Checkout);



