import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import axios from "../../../axios-orders";
import { connect } from "react-redux";

import Input from "../../../components/UI/Input/Input";
import Button from "../../../components/UI/Button/Button";
import Spinner from "../../../components/UI/Spinner/Spinner";
import withErrorHandler from "../../../hoc/withErrorHandler/withErrorHandler";
import * as orderActions from "../../../store/actions/index";

import classes from "./ContactData.module.css";

class ContactData extends Component {
  state = {
    orderForm: {
      name: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Your name",
        },
        value: "",
        validation: {
          required: true,
          minLength: 5,
        },
        valid: false,
        touched: false,
        errorMessage: "Please, enter your full Name",
      },
      email: {
        elementType: "input",
        elementConfig: {
          type: "email",
          placeholder: "Your e-mail",
        },
        value: "",
        validation: {
          required: true,
          minLength: 8,
        },
        valid: false,
        touched: false,
        errorMessage: "Please, enter something like: test@test.com",
      },
      address: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "e.g: La vie Rose 1234",
        },
        value: "",
        validation: {
          required: true,
        },
        valid: false,
        touched: false,
        errorMessage: "Please, enter a valid address",
      },
      zipCode: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "e.g: 1111",
        },
        value: "",
        validation: {
          required: true,
          minLength: 2,
          maxLength: 8,
        },
        valid: false,
        touched: false,
        errorMessage: "Please, enter a valid ZIP Code",
      },
      country: {
        elementType: "input",
        elementConfig: {
          type: "text",
          placeholder: "Your country",
        },
        value: "",
        validation: {
          required: true,
        },
        valid: false,
        touched: false,
        errorMessage: "Please, enter your country",
      },
      deliveryMethod: {
        elementType: "select",
        elementConfig: {
          options: [
            { value: "fatest", displayValue: "Fatest" },
            { value: "cheapest", displayValue: "Cheapest" },
          ],
        },
        value: "",
      },
    },
    loading: false,
    isValid: false,
  };

  checkValidity = (inputIdentifier, value, rules) => {
    let isValid = true;

    if (inputIdentifier !== "deliveryMethod") {
      if (rules.required) isValid = value.trim() !== "" && isValid;
      if (rules.minLength) isValid = value.length >= rules.minLength && isValid;
      if (rules.maxLength) isValid = value.length <= rules.maxLength && isValid;
    }

    return isValid;
  };

  inputChangeHandler = (e, inputIdentifier) => {
    const formCopy = { ...this.state.orderForm };
    const inputCopy = { ...formCopy[inputIdentifier] };
    let formIsvalid;
    let count = 0;
    inputCopy.value = e.target.value;
    inputCopy.valid = this.checkValidity(
      inputIdentifier,
      inputCopy.value,
      inputCopy.validation
    );
    inputCopy.touched = true;
    formCopy[inputIdentifier] = inputCopy;
    Object.entries(formCopy).forEach((input) => {
      if (input[1].valid) count++;
    });

    if (count === Object.entries(formCopy).length) {
      formIsvalid = true;
    }

    this.setState({ orderForm: formCopy, isValid: formIsvalid });
  };

  submitHandler = (e) => {
    e.preventDefault();
    const formData = {};

    for (let inputIdentifier in this.state.orderForm) {
      formData[inputIdentifier] = this.state.orderForm[inputIdentifier].value;
    }

    const order = {
      ingredients: this.props.ingredients,
      price: +this.props.totalPrice.toFixed(2),
      customer: formData,
      userId: this.props.userId,
    };

    this.props.onOrderBurger(order, this.props.token);
  };

  render() {
    const formElementsArray = Object.entries(this.state.orderForm).map(
      (arr) => {
        return {
          key: arr[0],
          elementType: arr[1].elementType,
          elementConfig: arr[1].elementConfig,
          value: arr[1].value,
          valid: arr[1].valid,
          validation: arr[1].validation,
          touched: arr[1].touched,
          errorMessage: arr[1].errorMessage,
        };
      }
    );

    let form = (
      <form onSubmit={this.submitHandler}>
        {formElementsArray.map((el) => {
          return (
            <Input
              key={el.key}
              elementType={el.elementType}
              elementConfig={el.elementConfig}
              value={el.value}
              invalid={!el.valid}
              shouldValidate={el.validation}
              isTouched={el.touched}
              errorMessage={el.errorMessage}
              changed={(event) => this.inputChangeHandler(event, el.key)}
            />
          );
        })}
        <div>
          <Button btnType={"Success"} disabled={!this.state.isValid}>
            ORDER
          </Button>
        </div>
      </form>
    );

    if (this.props.loading) form = <Spinner />;

    return (
      <div className={classes.ContactData}>
        <h4>Enter your contact Data</h4>
        {form}
        {this.props.redirect ? <Redirect to="/" /> : null}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    ingredients: state.burgerBuilder.ingredients,
    totalPrice: state.burgerBuilder.totalPrice,
    loading: state.order.loading,
    redirect: state.order.redirect,
    token: state.auth.token,
    userId: state.auth.userId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onOrderBurger: (orderData, token) =>
      dispatch(orderActions.purchaseBurger(orderData, token)),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withErrorHandler(ContactData, axios));



